.links {
  background-image: linear-gradient(to right, #000, #000, #000);
  background-size: 200% 100%;
  background-position: -100%;
  display: inline-block;
  padding: 5px 0;
  position: relative;
  text-decoration: none;
  font-family: 'Comfortaa', cursive;
  font-size: larger;
  line-height: 1.4rem;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: all 0.3s ease-in-out;
}

.links:before {
  content: '';
  background: linear-gradient(
    to right,
    rgba(255, 0, 0, 1),
    rgba(255, 0, 180, 1),
    rgba(0, 100, 200, 1)
  );
  display: block;
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 0;
  height: 3px;
  transition: all 0.3s ease-in-out;
}

.links:hover {
  background-position: 0;
  background-image: linear-gradient(
    to right,
    rgba(255, 0, 0, 1),
    rgba(255, 0, 180, 1),
    rgba(0, 100, 200, 1)
  );
}

.links:hover::before {
  width: 100%;
}

.icon-img {
  height: 28px;
  margin-bottom: 15px;
}
.icon-img:hover {
  scale: 1.2;
  cursor: pointer;
}
.heart {
  color: red;
}

